import React from 'react';
import m from './matcha.module.css';

const Matcha = props => {
  return (<div className={m.body}>
    <div className={m.name}>
      <h4>матча лате</h4>
    </div>
    <div className={m.table}>
      <table className={m.tbname}>
        <tbody>
          <tr>
            <td>класична</td>
          </tr>
          <tr>
            <td>троянлова</td>
          </tr>
        </tbody>
      </table>
      <table>
        <tbody>
          <tr>
            <td></td>
            <td>65</td>
            <td></td>
          </tr>
          <tr>
            <td></td>
            <td>69</td>
            <td></td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>)
};

export default Matcha;