import React from 'react'
import nav from './nav.module.css'
import {NavLink} from 'react-router-dom'

const Navigation = prop => {
  return (<div className={nav.nav}>
    <div>
      <NavLink to="/classic" activeClassName={nav.active}>Кава</NavLink>
    </div>
    <div>
      <NavLink to="/cold" activeClassName={nav.active}>Холодні напої</NavLink>
    </div>
    <div>
      <NavLink to="/desserts" activeClassName={nav.active}>Десерти</NavLink>
    </div>
  </div>)
}

export default Navigation
