import React from 'react';
import co from './cookies.module.css';

const Cookies = props => {
  return (
    <div className={co.appdes}>
      <h4>Печиво:</h4>
      <div className={co.body}>    
        <table>
          <tbody>
            <tr>
              <td><li>апельсинове</li></td>
            </tr>
            <tr>
              <td><li>шоколаднк</li></td>
            </tr>
            <tr>
              <td><li>з шоколадом</li></td>
            </tr>
            <tr>
              <td><li>вівсяне</li></td>
            </tr>
          </tbody>
        </table>
        <table className={co.price}>
          <tbody>
            <tr>
              <td>25</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>  
  )
}

export default Cookies;
