import './App.css';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Classic from './component/Classic/classic';
import Navigation from './component/Navigation/navigation';
import Cold from './component/Cold/cold';
import Desserts from './component/Desserts/desserts';

function App() {
  return (
    <Router>
      <div className="app">
        <div className="block"><p>mobile only</p></div>
        <Navigation/>
        <Switch>
          <Route exact path="/classic"
            render={() => <Classic />} />
          <Route exact path="/cold"
            render={() => <Cold />} />
          <Route exact path="/desserts"
            render={() => <Desserts />} />
        </Switch>
      </div>
    </Router>
  );
}

export default App;
