import React from "react"
import Blend from "../Blend/blend";
import Special from '../Special/special';
import Matcha from '../Matcha/match';
import c from './classic.module.css';

const Classic = (prop) => {
  return (<div className={c.app}>
            <div className={c.classic}>
              <div className={c.name}>
                <h4>моносорт</h4>
                <table>
                  <tbody className={c.headers}>

                    <tr className={c.size}>
                      <td>S</td>
                      <td>M</td>
                      <td>L</td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div className={c.table}>
                <table className={c.tbname}>
                  <tbody>
                    <tr>
                      <td>еспресо</td>
                    </tr>
                    <tr>
                      <td>допіо</td>
                    </tr>
                    <tr>
                      <td>фільтр кава</td>
                    </tr>
                    <tr>
                      <td>капучіно</td>
                    </tr>
                    <tr>
                      <td>лате</td>
                    </tr>
                    <tr>
                      <td>флет уайт</td>
                    </tr>
                  </tbody>
                </table>
                <table className={c.tbprice}>
                  <tbody>
                    <tr>
                      <td>22</td>
                      <td></td>
                      <td></td>
                    </tr>
                    <tr>
                      <td>24</td>
                      <td></td>
                      <td></td>
                    </tr>
                    <tr>
                      <td>28</td>
                      <td>35</td>
                      <td></td>
                    </tr>
                    <tr>
                      <td>32</td>
                      <td>38</td>
                      <td>42</td>
                    </tr>
                    <tr>
                      <td>32</td>
                      <td>38</td>
                      <td>42</td>
                    </tr>
                    <tr>
                      <td>42</td>
                      <td></td>
                      <td></td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
            <Blend />
            <Special />
            <Matcha />
          </div>)
}

export default Classic;