import React from 'react';
import mac from './mac.module.css';

const Macaron = props => {
  return (
    <div className={mac.appdes}>
      <h4>макаронси:</h4>
      <div className={mac.body}>    
        <table>
          <tbody>
            <tr>
              <td><li>фісташка</li></td>
            </tr>
            <tr>
              <td><li>солена карамель</li></td>
            </tr>
            <tr>
              <td><li>чорниця</li></td>
            </tr>
            <tr>
              <td><li>манго</li></td>
            </tr>
          </tbody>
        </table>
        <table className={mac.price}>
          <tbody>
            <tr>
              <td>35</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  )
};

export default Macaron;
