import React from 'react';
import maf from './mafin.module.css';

const Mafin = props => {
  return (<div>
    <div>
      <h4>мафіни:</h4>
      <div className={maf.body}>    
        <table>
          <tbody>
            <tr>
              <td><li>лимон</li></td>
            </tr>
            <tr>
              <td><li>шоколад</li></td>
            </tr>
          </tbody>
        </table>
        <table className={maf.price}>
          <tbody>
            <tr>
              <td>22</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div>
      <h4>еклери:</h4>
      <div className={maf.body}>    
        <table>
          <tbody>
            <tr>
              <td><li>ванільний</li></td>
            </tr>
            <tr>
              <td><li>шоколадний</li></td>
            </tr>
          </tbody>
        </table>
        <table className={maf.price}>
          <tbody>
            <tr>
              <td>35</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div>
      <h4>кіші:</h4>
      <div className={maf.body}>    
        <table>
          <tbody>
            <tr>
              <td><li>курка</li></td>
            </tr>
            <tr>
              <td><li>шинка</li></td>
            </tr>
            <tr>
              <td><li>шпинат</li></td>
            </tr>
          </tbody>
        </table>
        <table className={maf.price}>
          <tbody>
            <tr>
              <td>34</td>
            </tr>
            <tr>
              <td>36</td>
            </tr>
            <tr>
              <td>36</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
  )
};

export default Mafin;