import React from 'react';
import Cookies from './Cookies/cookies';
import des from './des.module.css';
import Macaron from './Macaron/macaron';
import Mafin from './Mafin/mafin';

const Desserts = props => {
  return (
    <div className={des.appdes}>
      <h4>тарти:</h4>
      <div className={des.body}>    
        <table>
          <tbody>
            <tr>
              <td><li>класичний</li></td>
            </tr>
            <tr>
              <td><li>з лохиною</li></td>
            </tr>
            <tr>
              <td><li>вишневий</li></td>
            </tr>
          </tbody>
        </table>
        <table className={des.price}>
          <tbody>
            <tr>
              <td>45</td>
            </tr>
            <tr>
              <td>60</td>
            </tr>
            <tr>
              <td>70</td>
            </tr>
          </tbody>
        </table>
      </div>
      <Cookies />
      <Macaron />
      <Mafin />
    </div>
  )
};

export default Desserts